.profile__name-block {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  gap: 18px;
  align-items: baseline;
}
/* во всех браузерах все ок кроме файрфокса, написал кастомное свойство. хоть и очень не хотел. лучше способа найти так и не смог 🥲 */
@-moz-document url-prefix() {
  .profile__name-block {
    align-items: center;
  }
}

@media screen and (max-width: 768px) {
  .profile__name-block {
    padding-left: calc(24px + 18px);
  }

}

@media screen and (max-width: 425px) {
  .profile__name-block {
    padding-left: calc(18px + 10px);
    gap: 16px;
  }
}