.profile__button_type_add {
  margin: 0;
  padding: 0;
  width: 150px;
  height: 50px;
  border-width: 2px;
  border-radius: 2px;
  background-image: url('/src/styles/images/icons/icon__add.svg');
  background-size: 22px 22px;
}

@media screen and (max-width: 768px) {
  .profile__button_type_add {
    width: 100%;
  }
}

@media screen and (max-width: 425px) {
  .profile__button_type_add {
    background-size: 16px 16px;
  }
}